import React from 'react'
import { useSelector } from 'react-redux'
import { isLoaded, isEmpty } from 'react-redux-firebase'

import GMap from '@bit/nexxtway.react-rainbow.g-map'
import MapMarker from '@bit/nexxtway.react-rainbow.map-marker'

import Layout from '../components/layout'
import Image from '../components/image'
import SEO from '../components/seo'
import FirebaseAuth from '../components/firebaseAuth'

const GOOGLE_MAP_KEY = process.env.GATSBY_GOOGLE_MAP_KEY

const IndexPage = () => {
  const user = useSelector(state => state.firebase.auth)
  return (
    <Layout>
      <SEO title="Home" />
      <FirebaseAuth />
      <GMap
        apiKey={GOOGLE_MAP_KEY}
        zoom={10}
        latitude={-33.836538}
        longitude={151.1279}>
        <MapMarker
          latitude={-33.941264}
          longitude={151.2042969}
          label="Botany Bay"
          description=" Botany, New South Wales, Australia"
        />
        <MapMarker
          latitude={-33.940004}
          longitude={151.094593}
          label="Garema"
          description="Garema Circuit, Kingsgrove, Australia"
        />
      </GMap>
      {!isLoaded(user) ? (
        <span>Loading...</span>
      ) : isEmpty(user) ? (
        <span>Not Authed</span>
      ) : (
        <pre>{JSON.stringify(user, null, 2)}</pre>
      )}

      <p>
        Made with <span style={{ color: '#e25555' }}>&#9829;</span> in Chile
      </p>
    </Layout>
  )
}

export default IndexPage
